























import Vue from "vue";
import Component from "vue-class-component";
import router from "@/router";
import DesktopSearchBox from "./components/DesktopSearchBox.vue";
import SearchBox from "./components/SearchBox.vue";
import TopMenuUser from "./components/TopMenuUser.vue";
import MobileHeader from "./components/MobileHeader.vue";
import DesktopRightSidebar from "./components/DesktopRightSidebar.vue";
import { AppService } from "./service/app.service";

@Component({
  components: {
    DesktopSearchBox,
    SearchBox,
    TopMenuUser,
    MobileHeader,
    DesktopRightSidebar,
  },
})
export default class App extends Vue {
  app = AppService.instance;
  mounted(): void {
    console.log("App::mounted. running mode: ", process.env.NODE_ENV);
    this.app.portal();

    router.afterEach((to) => {
      // console.log(to);
      if (to.name == "PostList" || to.name == "PostView") {
        this.app.hideLeftSidebar();
      } else {
        this.app.showLeftSidebar();
      }
    });
  }
}
